import {
    LineChart,
    Line,
    CartesianGrid,
    XAxis,
    YAxis,
    ReferenceArea,
    ResponsiveContainer,
    Tooltip,
    Legend,
    ReferenceLine,
    Label
} from 'recharts';
import moment from "moment";
import PropTypes from "prop-types";

MswForecastGraph.propTypes = {location: PropTypes.any, forecastData: PropTypes.any, isMini: PropTypes.bool,}
export function MswForecastGraph(props) {
    if(props.forecastData == null || props.location == null) {
        return <>
            <div>Detailed Forecast not possible at the moment...</div>
        </>
    }
    function normalizeData(forecastData) {
        let normalizedData = [];
        for(let curveIndex = 0; curveIndex < forecastData.data.length; curveIndex++) {
            let foreCastCurve = forecastData.data[curveIndex];
            for(let curveDatapointIndex = 0; curveDatapointIndex < foreCastCurve.x.length; curveDatapointIndex++) {
                let obj = {};
                obj["datetime"] = moment(foreCastCurve.x[curveDatapointIndex]).valueOf();
                obj["forecastFlow" + curveIndex] = foreCastCurve.y[curveDatapointIndex]
                normalizedData.push(obj);
            }
        }
        return normalizedData;
    }

    let normalizedGraphData = normalizeData(props.forecastData);

    const from = normalizedGraphData[0].datetime;
    const to = normalizedGraphData[normalizedGraphData.length-1].datetime;
    let firstDayMidnight = new Date(from).setHours(0, 0, 0, 1);
    let firstDayMidnightDate = new Date(firstDayMidnight);
    const ticks = [
        firstDayMidnight,
        firstDayMidnightDate.setHours(24, 0, 0, 0),
        firstDayMidnightDate.setHours(24, 0, 0, 0),
        firstDayMidnightDate.setHours(24, 0, 0, 0),
        firstDayMidnightDate.setHours(24, 0, 0, 0),
        firstDayMidnightDate.setHours(24, 0, 0, 0)
    ]

    if(props.isMini === true) {
        return <>
            <ResponsiveContainer width="100%" aspect={1.5}>
                <LineChart minWidth={0} data={normalizedGraphData} ticks={1}>
                    <ReferenceArea y1={props.location.min} y2={props.location.max} ifOverflow="extendDomain" fill="green" />
                    <Line type="monotone" dataKey="forecastFlow2" stroke="blue" dot={false} />
                    <Line type="monotone" dataKey="forecastFlow3" stroke="orange" dot={false} />
                    <Line type="monotone" dataKey="forecastFlow4" stroke="green" dot={false} />
                    <CartesianGrid />
                    <XAxis
                        type="number"
                        dataKey="datetime"
                        domain={[from, to]}
                        scale="time"
                        ticks={ticks}
                        tickFormatter={v => moment(v).format("DD") + "."}
                        minTickGap={1}
                        hide={true}/>
                </LineChart>
            </ResponsiveContainer>
        </>
    }

    return <>
        <ResponsiveContainer width="100%" aspect={1.5}>
            <LineChart width={450} height={300} data={normalizedGraphData} ticks={1}>
                <ReferenceArea y1={props.location.min} y2={props.location.max} ifOverflow="extendDomain" fill="green" />
                <ReferenceLine y={props.location.min} >
                    <Label value={props.location.min} position="insideRight"/>
                </ReferenceLine>
                <ReferenceLine y={props.location.max} >
                    <Label value={props.location.max} position="insideRight"/>
                </ReferenceLine>
                <Line type="monotone" dataKey="forecastFlow2" stroke="blue" dot={false} name={props.forecastData.data[2].name} />
                <Line type="monotone" dataKey="forecastFlow3" stroke="orange" dot={false} name={props.forecastData.data[3].name} />
                <Line type="monotone" dataKey="forecastFlow4" stroke="green" dot={false} name={props.forecastData.data[4].name} />
                <CartesianGrid />
                <Tooltip labelFormatter={v => moment(v).format("DD.MM. hh:00")} />
                <XAxis
                    type="number"
                    dataKey="datetime"
                    domain={[from, to]}
                    scale="time"
                    ticks={ticks}
                    tickFormatter={v => moment(v).format("DD") + "."} />
                <YAxis />
                {/* payload is only necessary to get rid of unneccessary double legend entry because forecastFlow0 and forecastFlow1 are both named Min/Max */}
                <Legend
                    payload={[
                        {id:"forecastFlow0", type: "line", value:"Gemessen", color:"green"},
                        {id:"forecastFlow3", type: "line", value:"25.-75. Perzentil", color:"blue"},
                        {id:"forecastFlow4", type: "line", value:"Median", color:"orange"},
                    ]}
                />
            </LineChart>
        </ResponsiveContainer>
    </>
}