import * as PropTypes from "prop-types";
import moment from "moment";
import arrow_down from "./assets/arrow_down.png";
import {MswForecastGraph} from "./MswForecastGraph";

MswForecast.propTypes = {flow: PropTypes.number, location: PropTypes.any, forecastData: PropTypes.any};

export function MswForecast(props) {
  let content;
  if(props.forecastData == null) {
    content = <><div>Forecast unavailable</div></>
  } else {
    content = <>
      <div className="miniForecast">
        <MswForecastGraph className="miniForecast"
                          location={props.location}
                          forecastData={props.forecastData}
                          isMini={true}/>
      </div>
      <span className="collapsibleIcon">
      <img alt="extend forecast" src={arrow_down}/>
    </span>
    </>;
  }

  return <>
    <div className="forecast">
      <div className="forecastContainer">
        {content}
      </div>
    </div>
  </>
}
