import {MswForecast} from "./MswForecast";
import * as PropTypes from "prop-types";
import {MswForecastGraph} from "./MswForecastGraph";
import {useEffect, useState} from "react";

// API might not return a specific measurement
// in which case the .find() returns "undefined"
function check_if_meas_undefined(meas) {
  if (meas === undefined) {
    meas = "N/A"; // 🤷
  } else {
    meas = meas.val;
  }
  return meas;
}

function flow2stars(_flow, _location) {
  if(_location.ranking === undefined) return undefined;
  if(_flow < _location.min || _flow > _location.max) return 0;
  else if(_flow < _location.ranking.oneStarMax) return 1;
  else if(_flow < _location.ranking.twoStarMax) return 2;
  else if(_flow < _location.ranking.threeStarMax) return 3;
  else if(_flow < _location.ranking.fourStarMax) return 4;
  else return 5;
}

// convert flow value to color
function flow2color(_flow, _location) {
  if (_flow < _location.min) {
    return "flow_bad";
  }
  if (_flow < _location.optimum) {
    return "flow_ok";
  }
  // check if it's a riversurf location
  if ("max" in _location) {
    if (_flow < _location.optimum_max) {
      return "flow_good";
    }
    if (_flow < _location.max) {
      return "flow_ok";
    }
    if (_flow < _location.danger) {
      return "flow_bad";
    }
  } else if (_flow < _location.danger) {
    return "flow_good";
  }
  return "flow_danger";
}

// convert temperature value to color
function temp2color(_temp) {
  if (_temp < 8) {
    return "temp_0";
  }
  if (_temp < 13) {
    return "temp_1";
  }
  if (_temp < 18) {
    return "temp_2";
  }
  if (_temp < 23) {
    return "temp_3";
  }
  return "temp_4";
}

Stars.propTypes = {displayStars: PropTypes.bool};
function Stars(props) {
  if(!props.displayStars) {
    return;
  }

  let isInvisible = false;
  let nrOfStars = props.nrOfStars;

  if(nrOfStars === undefined) {
    nrOfStars = 0;
    isInvisible = true;
  }

  const stars = Array.from(Array(5).keys()).map(i => {
    return i < nrOfStars ? <FilledStar key={i} /> : <EmptyStar key={i} />
  });
  return <div key={nrOfStars} className={"stars " + (isInvisible ? 'invisible' : '')}>{stars}</div>;

  function FilledStar() {
    return <div className="filledStar"> ★ </div>;
  }

  function EmptyStar() {
    return <div className="emptyStar"> ✰ </div>;
  }
}

function Measurements(props) {
  let measurementClass = "measurements";
  if(props.displayStars === false) {
    measurementClass += " doubleCol";
  }
  return <>
    <div className={measurementClass}>
      {/* first row */}
      <div className="flow meas">
        <div className={props.flow_color}>{props.flow}</div>
        <div className="unit smallFont">
          m<sup>3</sup>/s
        </div>

        {/* add warning sign if flow reaches dangerous levels */}
        {props.flow_color === "flow_danger" && (
            <div
                className="danger"
                title="Moderate flood danger, be careful!"
            >
              &ensp; ⚠️
            </div>
        )}
      </div>
      {/* second row */}
      <div className={isNaN(props.temp)? "noDisplay" : "temp meas"}>
        <div className={props.temp_color}>{props.temp}</div>
        <div className="unit">°C</div>
      </div>
    </div>
  </>;
}

function Spot(name, link, flow_color, flow, temp_color, temp, nrOfStars, location, displayStars) {
  // define states for fetching forecastData
  const [forecastData, setForecastData] = useState(null);
  const [hasFetched, setHasFetched] = useState(false);

  const fetchForecastData = async(locationId) => {
    if(!hasFetched) {
      let fetch_url = `https://europe-west6-windy-anchor-328011.cloudfunctions.net/msw_forecast?locationId=${locationId}`;
      try {
        const response = await fetch(fetch_url);
        let responseData = await response.json();
        if (!responseData.plot) {
          throw new Error(
              `This is an HTTP error: The status is ${response.status}`
          );
        }
        setForecastData(responseData.plot);
      } catch (err) {
        setForecastData(null);
      }
      setHasFetched(true);
    }
  };

  // empty deps array is here to make sure useEffect is only called once. Maybe there's a better way to do this
  useEffect(() => {
    fetchForecastData(location.id).then(() => console.log("forecast data fetched for " + name));
  }, []);

  let collapsibleContent = <></>
  if(forecastData != null) {
    collapsibleContent = <>
      <div className="collapsibleContent">
        <h2>Forecast</h2>
        <MswForecastGraph location={location} forecastData={forecastData}/>
      </div>
    </>;
  }

  return <>
    <details key={name} className="spot">
      <summary className="spotname">
        <a href={link} target="_blank" rel="noreferrer">
          {name}
        </a>
        <Measurements flow_color={flow_color}
                      flow={flow}
                      temp_color={temp_color}
                      temp={temp}
                      displayStars={displayStars}/>
        <Stars nrOfStars={nrOfStars} displayStars={displayStars}></Stars>
        <MswForecast flow={flow} location={location} forecastData={forecastData}/>
      </summary>
      {collapsibleContent}
    </details>
  </>;
}

export function SpotList(props) {
  const N = props.locations.length;

  // combine props.flowAndTempData and locations into a single Array to render
  let render_array = Array(N);
  for (let i = 0; i < N; i++) {
    // find the right measurements
    let flow_i = props.flowAndTempData.find(
      ({ loc, par }) => loc === props.locations[i].id && par === "flow"
    );
    flow_i = check_if_meas_undefined(flow_i);
    let temp_i = props.flowAndTempData.find(
      ({ loc, par }) => loc === props.locations[i].id && par === "temperature"
    );
    temp_i = check_if_meas_undefined(temp_i);

    // color-code flow and temperature
    let flow_color_i = flow2color(flow_i, props.locations[i]);
    let temp_color_i = temp2color(temp_i);
    let nrOfStars = flow2stars(flow_i, props.locations[i]);

    if(!props.locations[i].isSecret || props.includeSecretSpots) {
      // then put them into a new container with all info for rendering
      render_array.push({
        locationId: props.locations[i].id,
        name: props.locations[i].name,
        link:
            "https://www.hydrodaten.admin.ch/de/seen-und-fluesse/stationen-und-daten/" +
            props.locations[i].id,
        flow: Math.round(flow_i),
        flow_color: flow_color_i,
        temp: Math.round(temp_i),
        temp_color: temp_color_i,
        nrOfStars: nrOfStars,
        location: props.locations[i]
      });
    }
  }

  return (
    <div className="spotlist">
      {render_array.map(
        ({ location, name, link, flow, flow_color, temp, temp_color, nrOfStars }) => (
          Spot(name, link, flow_color, flow, temp_color, temp, nrOfStars, location, props.displayStars)
        )
      )}
    </div>
  );
}
